// Category icons for placeholders
const getCategoryIcon = (category: string) => {
  const icons: Record<string, string> = {
    general: '📰',
    technology: '💻',
    recruitment: '👥',
    analytics: '📊',
    diversity: '🌈',
    wellbeing: '💚',
    business: '💼',
  };
  return icons[category] || '📄';
};

const fetchNews = async ({
  queryKey,
}: {
  queryKey: [string, string, string, number];
}) => {
  const [, category, searchTerm, currentPage] = queryKey;
  const BACKEND_URL =
    process.env.NODE_ENV === "development"
      ? "http://localhost:3000"
      : process.env.REACT_APP_BACKEND_URL || "https://bug-free-winner-production.up.railway.app";

  const limit = 20;
  const offset = (currentPage - 1) * limit;

  const url = searchTerm
    ? `${BACKEND_URL}/api/rss/search?q=${encodeURIComponent(searchTerm)}&limit=${limit}&offset=${offset}`
    : `${BACKEND_URL}/api/rss/articles?category=${category}&limit=${limit}&offset=${offset}`;

  const response = await axios.get(url);
  return {
    articles: response.data.articles || [],
    pagination: response.data.pagination || { hasNext: false, hasPrev: false },
    total: response.data.total || 0
  };
};

const NewsAggregator: React.FC = () => {
  const [category, setCategory] = useState<string>(() => {
    return localStorage.getItem("newsCategory") || "general";
  });

  const [searchTerm, setSearchTerm] = useState<string>(() => {
    return localStorage.getItem("newsSearch") || "";
  });

  const [currentPage, setCurrentPage] = useState<number>(() => {
    return Number(localStorage.getItem("newsPage")) || 1;
  });

  const {
    data: newsData,
    isLoading: loading,
    error,
  } = useQuery({
    queryKey: ["news", category, searchTerm, currentPage],
    queryFn: fetchNews,
    staleTime: 5 * 60 * 1000,
    refetchOnWindowFocus: false,
  });

  const news = newsData?.articles || [];
  const pagination = newsData?.pagination || { hasNext: false, hasPrev: false };
  const totalArticles = newsData?.total || 0;

  const handleCategoryButtonClick = (newCategory: string) => {
    if (newCategory !== category) {
      setCategory(newCategory);
      setCurrentPage(1);
      localStorage.setItem("newsCategory", newCategory);
      localStorage.setItem("newsPage", "1");
    }
  };

  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setSearchTerm(value);
    localStorage.setItem("newsSearch", value);
  };

  const handleNextPage = () => {
    setCurrentPage((prev) => {
      const newPage = prev + 1;
      localStorage.setItem("newsPage", newPage.toString());
      return newPage;
    });
  };

  const handlePrevPage = () => {
    setCurrentPage((prev) => {
      const newPage = Math.max(prev - 1, 1);
      localStorage.setItem("newsPage", newPage.toString());
      return newPage;
    });
  };

  // Determine card sizes (MSN-style mixed layout)
  const getCardSize = (index: number) => {
    // First card is always large
    if (index === 0) return 'large';
    // Cards 1-2 are wide
    if (index === 1 || index === 2) return 'wide';
    // Every 7th card is tall
    if ((index - 3) % 7 === 0) return 'tall';
    // Rest are normal
    return 'normal';
  };

  const currentDate = new Date().toLocaleDateString('en-US', {
    weekday: 'long',
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  });

  return (
    <AppContainer>
      <Header>
        <HeaderContent>
          <LogoSection>
            <Title>EvalMatch News Hub</Title>
          </LogoSection>
          <HeaderRight>
            <DashboardLink href="/dashboard">
              <svg width="16" height="16" viewBox="0 0 16 16" fill="currentColor">
                <path d="M6 2a1 1 0 00-1 1v2a1 1 0 001 1h4a1 1 0 001-1V3a1 1 0 00-1-1H6zM6 7a1 1 0 00-1 1v6a1 1 0 001 1h4a1 1 0 001-1V8a1 1 0 00-1-1H6zM2 10a1 1 0 011-1h1a1 1 0 011 1v4a1 1 0 01-1 1H3a1 1 0 01-1-1v-4zM12 10a1 1 0 011-1h1a1 1 0 011 1v4a1 1 0 01-1 1h-1a1 1 0 01-1-1v-4z"/>
              </svg>
              Dashboard
            </DashboardLink>
            <TimeStamp>{currentDate}</TimeStamp>
          </HeaderRight>
        </HeaderContent>
      </Header>

      <MainContent>
        <SearchContainer>
          <SearchWrapper>
            <SearchIcon>
              <svg width="20" height="20" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd" />
              </svg>
            </SearchIcon>
            <SearchBox
              type="text"
              placeholder="Search HR news..."
              value={searchTerm}
              onChange={handleSearchChange}
            />
          </SearchWrapper>
        </SearchContainer>

        <TabNavigation>
          <TabContainer>
            {[
              "general",
              "technology",
              "recruitment",
              "analytics",
              "diversity",
              "wellbeing",
              "business",
            ].map((cat) => (
              <TabButton
                key={cat}
                onClick={() => handleCategoryButtonClick(cat)}
                $active={category === cat}
              >
                {cat.charAt(0).toUpperCase() + cat.slice(1)}
              </TabButton>
            ))}
          </TabContainer>
        </TabNavigation>

        {error ? (
          <ErrorState>
            <strong>Error:</strong> {(error as Error).message}
          </ErrorState>
        ) : loading ? (
          <LoadingState>
            <LoadingSpinner />
            <LoadingText>Loading the latest HR news...</LoadingText>
          </LoadingState>
        ) : (
          <>
            <NewsGrid>
              {news.map((article: any, index: number) => {
                const cardSize = getCardSize(index);
                const hasImage = !!article.image_url;
                const articleCategory = article.category || category;
                
                return (
                  <NewsCard 
                    key={article.id || article.url}
                    $hasImage={hasImage}
                    $gridSize={cardSize}
                    as="a"
                    href={article.url}
                    target="_blank"
                    rel="noopener noreferrer"
                    aria-label={`Read more: ${article.title}`}
                  >
                    <CardImageContainer 
                      $gradient={!hasImage ? categoryGradients[articleCategory] : undefined}
                      $size={cardSize}
                    >
                      {hasImage ? (
                        <CardImage 
                          src={article.image_url} 
                          alt={article.title}
                          loading="lazy"
                        />
                      ) : (
                        <PlaceholderContent>
                          <PlaceholderIcon>
                            {getCategoryIcon(articleCategory)}
                          </PlaceholderIcon>
                          <PlaceholderCategory>
                            {articleCategory === 'general' ? 'NEWS' : articleCategory.toUpperCase()}
                          </PlaceholderCategory>
                        </PlaceholderContent>
                      )}
                    </CardImageContainer>
                    
                    <CardContent $size={cardSize}>
                      <CardMeta>
                        <CardSource>{article.feed_name}</CardSource>
                        {article.published_at && (
                          <CardDate>
                            {new Date(article.published_at).toLocaleDateString('en-US', {
                              month: 'short',
                              day: 'numeric',
                            })}
                          </CardDate>
                        )}
                      </CardMeta>
                      
                      <CardTitle $size={cardSize}>{article.title}</CardTitle>
                      
                      {article.description && (
                        <CardDescription $size={cardSize}>
                          {article.description}
                        </CardDescription>
                      )}
                    </CardContent>
                  </NewsCard>
                );
              })}
            </NewsGrid>

            <PaginationContainer>
              <PaginationButton
                disabled={!pagination.hasPrev}
                onClick={handlePrevPage}
              >
                <svg width="16" height="16" viewBox="0 0 16 16" fill="currentColor">
                  <path fillRule="evenodd" d="M11.354 1.646a.5.5 0 010 .708L5.707 8l5.647 5.646a.5.5 0 01-.708.708l-6-6a.5.5 0 010-.708l6-6a.5.5 0 01.708 0z"/>
                </svg>
                Previous
              </PaginationButton>
              
              <PaginationInfo>
                Page {currentPage} {pagination.totalPages && `of ${pagination.totalPages}`}
                {totalArticles > 0 && (
                  <div style={{ fontSize: '12px', color: '#999', marginTop: '2px' }}>
                    {totalArticles} total articles
                  </div>
                )}
              </PaginationInfo>
              
              <PaginationButton
                disabled={!pagination.hasNext}
                onClick={handleNextPage}
              >
                Next
                <svg width="16" height="16" viewBox="0 0 16 16" fill="currentColor">
                  <path fillRule="evenodd" d="M4.646 1.646a.5.5 0 01.708 0l6 6a.5.5 0 010 .708l-6 6a.5.5 0 01-.708-.708L10.293 8 4.646 2.354a.5.5 0 010-.708z"/>
                </svg>
              </PaginationButton>
            </PaginationContainer>
          </>
        )}
      </MainContent>
    </AppContainer>
  );
};

export default NewsAggregator;